<template>
	<section class="wrap has_header_wrap symptom_wrap">
		<section class="wrap_in">
			<!-- 头部标题 -->
			<com-header titles="对症找药">
				<template  slot="style-def"></template>
			</com-header>
			<!-- 头部标题 end -->
			
			<!-- 热门搜索 -->
			<div class="symptom">
				<div class="border_h">
					<span>病种</span>
				</div>
				<div class="lists">
					<!-- <a 
						v-for="(hs,index) in hotSear" 
						@click="jumpSearch(hs,index)"
						href="javscript:;">{{ hs.name }}</a> -->
					<a 
						v-for="(hs,index) in lists" 
						@click="jumpSearch(hs,index)"
						href="javscript:;">{{ hs }}</a>
				</div>
			</div>
			<!-- 热门搜索 end -->
		</section>

		<!-- 子页面 -->
	    <transition name="fade">
	    	<router-view v-on:changeClass="changeClassOne"></router-view>
	    </transition>
	    <!-- 子页面 end -->
	</section>
</template>
<script>
import { Header, Button, Badge, Toast } from 'mint-ui';
import comHeaderTitle from '@/components/Common/ComHeaderTitle'
import comFoot from '@/components/Common/ComFoot';

export default {
	name: 'symptom',
	data() {
		return {
	      isUnscRollable: false, //是否页面不可滚动
	      backParentPage: false, //是否回到父页面
	      hotSear: [ //热门搜索
	      	{ id: 1, name: '人血白蛋白' },
	      	{ id: 2, name: '京都念慈菴蜜炼川贝枇杷膏' },
	      	{ id: 3, name: '伊迈格' },
	      	{ id: 4, name: '风湿祛痛胶囊' },
	      	{ id: 5, name: '辛伐他汀片' },
	      	{ id: 6, name: '欣海宁' },
	      ],
	      lists: [],//对症找药列表
		}
	},
	props: [
		// 'pclass',
	],
	components: {
		'mt-header': Header,
		'mt-button': Button,
		'mt-badge':  Badge,
		comFoot, // 公共底部
		comHeaderTitle, // 公共头部标题
	},
	created (){
		const _self = this;

		//获取疾病列表
		this.$ajax({
			// url: '/apiserver/product/searchALLDisease',
			url: _self.webRoot + '/product/searchALLDisease',
		})
		.then(function(res){
			// console.log(res)
			if(res.data){
				_self.lists = res.data;
			}else{
				// _self.$options.methods.showToast('暂无');
				console.log('疾病查询没有数据')
			}
		})
		.catch(function(err){
			console.log(err)
		});
	},
	mounted : function() {
		const _self = this;
		this.$emit('changeClass',false);
	},
	updated (){
	    this.$nextTick(function () {
	      if( !this.backParentPage ){
	        this.isUnscRollable = true;
	      }
	    }) 
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
  	methods: {
  		//跳转搜索
  		jumpSearch(hs,index) {
  			// this.$router.push({ path: '/product', query: { searchCon: hs } });
  			const _self = this;

			//获取疾病
			this.$ajax({
				// url: '/apiserver/product/searchByDisease',
				url: _self.webRoot + '/product/searchByDisease',
				data: {
					disease: hs
				}
			})
			.then(function(res){
				// console.log('根据疾病查询商品接口: ', res)
				if(res.data.success){
					var productId = res.data.data[0].productID;
					_self.$router.push({ path: '/product_detail', query: { proid: productId } });
				}else{
					_self.$options.methods.showToast(res.data.msg);
				}
			})
			.catch(function(err){
				console.log(err)
			});
  			//this.$router.push({ path: '/product_detail', query: { proid: hs } });
  		},

  		//公共函数
	    //显示提示信息
	    showToast( msg, time ){
	      var time = time ? time : 2000;

	      Toast({
	        message: msg,
	        position: 'center',
	        duration: time
	      });     
	    },
	    // 判断是否返回父页面
	    changeClassOne ( isChildDestroyed ){
	      if( isChildDestroyed ){ 
	        //退出子页面
	        this.backParentPage = true; //返回首页
	        this.isUnscRollable = false;
	      }else{
	        //没退出子页面
	        this.backParentPage = false; // 没返回首页
	        this.isUnscRollable = true;
	      }
	    },
  	}
}
</script>

<style scoped>
	@import './Css/symptom.css';
</style>


